.home{

    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: column;



}

html{

    scroll-behavior: smooth;

}


@media only screen and (max-width:1024px)
{

    .home{

        width: 90%;
    
    }



}


@media only screen and (max-width:512px)
{

    .home{

        width: 100%;
        
    
    }



}


